@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500;600&display=swap');
@import '../../style/variables.scss';
*{
    font-family: 'Rubik', sans-serif;
    margin:0; padding:0;
    box-sizing: border-box;
    outline: none; border:none;
    text-decoration: none;
    transition: .2s linear;
    font-size: 62.5%;
    
   scroll-behavior: smooth;
 }
section{
    max-width: 1200px;
    margin:0 auto;
    padding:2rem;
 }

.carousel {
    
    background-color: $standardna;
}

.brands {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 2px solid $tamna-braon;
    border-bottom: 2px solid $tamna-braon;
    .circle {
        width: 17rem;
        height: 17rem;
        margin: 0 .5rem;
        padding: 1rem;
        img{
            width: 100%;
            height: 100%;
            border-radius: 50%;    
    }   
    }
    .circle:hover{
        opacity: 0.5;
    }
}

@media (max-width:300px) {
    * {
        font-size: 30%;
    }
    
    .brands {
       display: none;
    }
}
@media (min-width:301px) and (max-width:416px) {
    * {
        font-size: 30%;
    }
    .brands{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 5px;
        grid-row-gap: 5px;
    }
    section {
        padding: 0;
    }
}
@media (min-width:412px) and (max-width: 576px) {
    * {
        font-size: 42%;
    }
    .brands{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-column-gap: 5px;
        grid-row-gap: 5px;
    }
}

